<script>
import { orderGet, getFlowInfo, cancelOrder } from './../../../api/shop/order.js'
import changePrice from './changePrice.vue'
import Clipboard from 'clipboard'
import printPage from './shipBill.vue'
export default {

  data() {
    return {
      order: {},
      expressLoading: false,
      flowInfo: []
    }
  },
  components: {
    changePrice,
    printPage
  },
  methods: {
    copyText() {
      var clipboard = new Clipboard('#triggerCopy')
      const that = this
      clipboard.on('success', function(e) {
        that.$message({
          message: 'å¤å¶æå',
          type: 'success'
        })
        e.clearSelection()
      })
    },
    updateList(status) {
      if (status) {
        this.getOrderInfo(this.$route.query.id)
      }
    },
    setChangPrice(item) {
      this.$refs.changePrice.setChangPrice(item)
    },
    cancelOrder(orderId) {
      this.$confirm('æ¯å¦ç¡®è®¤åæ¶è¯¥è®¢å', 'æç¤º', {
        confirmButtonText: 'ç¡®å®',
        cancelButtonText: 'åæ¶',
        type: 'warning'
      })
        .then(res => {
          cancelOrder({ orderId: orderId }).then(res => {
            if (res.code === 200) {
              this.$successMessage('åæ¶æå')
              this.getOrderInfo(this.$route.query.id)
            } else {
              this.$errorMessage(res.message)
            }
          })
        })
        .catch(() => {})
    },
    printOrder(id) {
      this.$refs.print.getOrderInfo(id).then(res => {
        var printStr = res.innerHTML
        let printUrl = ''
        if (process.env.NODE_ENV === 'production') {
          const path = location.pathname.substring(0, location.pathname.lastIndexOf('/'))
          printUrl = path + '/static/print/print.html'
        } else {
          printUrl = '/static/print/print.html'
        }
        var page = window.open(printUrl, '')
        setTimeout(() => {
          page.document.body.innerHTML = printStr
          return false
        }, 50)
      })
    },
    getOrderInfo(id) {
      this.loading = true
      // è·åè®¢åä¿¡æ¯
      orderGet({ id: id }).then(res => {
        if (res.code === 200) {
          this.order = res.data
          this.loading = false
        }
      })
    },
    getFlowInfo(orderId) {
      if (this.flowInfo.length <= 0) { // ä¸è¦ä¸ç´å»è¯·æ±
        this.expressLoading = true
        getFlowInfo({ orderId: orderId }).then(res => {
          if (res.code === 200) {
            if (res.data.traces.length > 0) {
              this.flowInfo = res.data.traces.reverse() // ç¿»è½¬ç©æµä¿¡æ¯
            } else {
              this.flowInfo = [{
                acceptTime: '',
                acceptStation: 'ææ ç©æµä¿¡æ¯'
              }]
            }
          } else {
            this.$errorMessage(res.message)
          }
          this.expressLoading = false
        })
      }
    }
  },
  filters: {
    handlerOrderStatus(val) {
      // äº¤æç¶æçè¿æ»¤å¨
      var orderStatus = [
        '',
        'å¾ä»æ¬¾',
        'å¾åè´§',
        'å¾æ¶è´§',
        'å·²å®æ',
        'å·²åæ¶',
        'è¶æ¶äº¤æå³é­'
      ]
      return orderStatus[parseInt(val)]
    },
    handlerType(val) {
      // è®¢åç±»åçè¿æ»¤å¨
      return val === 1 ? 'å®ç©è®¢å' : 'èæè®¢å'
    },
    handlerSource(val) {
      // è®¢åæ¥æºçè¿æ»¤å¨
      var sources = ['', 'PC', 'ææºç«¯', 'APP', 'å¬ä¼å·', 'å°ç¨åº', 'å¹³æ¿']
      return sources[parseInt(val)]
    },
    handlerPayMode(val) {
      // å¤çæ¯ä»æ¹å¼
      var payModes = ['', 'æ¯ä»å®', 'å¾®ä¿¡', 'é¶è', 'ä½é¢']
      return payModes[parseInt(val)]
    },
    handlerRefundStatus(val) {
      // å®åå¤çç¶æçè¿æ»¤å¨
      var refundStatus = [
        'å¾åå®¶ç¡®è®¤',
        'éæ¬¾ä¸­',
        'åå®¶æç»',
        '',
        'å¾ä¹°å®¶åè´§',
        'å¾åå®¶æ¶è´§',
        'å¾ä¹°å®¶æ¶è´§',
        'å¾åå®¶åè´§',
        'å·²å®æ',
        'ä¹°å®¶åæ¶'
      ]
      return refundStatus[parseInt(val)]
    }
  },
  created() {
    this.getOrderInfo(this.$route.query.id)
  }
}
</script>

