<template>
  <!-- ä¿®æ¹ä»·æ ¼ -->
  <el-dialog class="t-dialog" :visible="priceVisible" title="ä»·æ ¼ä¿®æ¹" :before-close="handleClose">
    <el-form :model="updatePriceForm" ref="form">
      <table class="order-table">
        <tr>
          <th>åå</th>
          <th style="text-align:left">åä»·</th>
          <th style="text-align:left">æ°é</th>
          <th style="text-align:left">å°è®¡</th>
          <th>ä¼æ ä¿¡æ¯</th>
        </tr>
        <tbody>
          <tr v-for="(product,index) in changeProductItems" :key="product.id">
            <td class="product-info">
              <div class="product__inner">
                <img
                  class="product-img"
                  :src="$cms.filterUrl(product.productInfo.coverImageUrl)"
                  v-if="product.productInfo"
                >
                <div class="product-title">
                  <h3
                    class="title-h3"
                    v-if="product.productInfo"
                  >{{product.productInfo.productName}}</h3>
                  <p
                    class="title-p"
                    style="margin-top:10px;"
                    v-if="product.productInfo"
                  >{{showAttr(product.productInfo.skuAttr)}}</p>
                </div>
              </div>
            </td>
            <td class="order-count">
              <span>ï¿¥{{$cms.float(product.productPrice)}}</span>
            </td>
            <td class="order-count">
              <span>x {{product.productCount}}</span>
            </td>
            <td class="order-count">
              <span>{{$cms.float(product.payAmount)}}</span>
            </td>
            <td class="order-count order-input">
              <el-form-item
              label-width="60px"
              label=""
                :prop="'items.' + index + '.discountAmount'"
                :rules="[
                {required: true, message: 'æ­¤é¡¹å¿å¡«', trigger: 'blur' },
                { validator: (rule, value, callback)=>{validatePrice(rule, value, callback,product.payAmount)},  trigger:'blur' },
                { type: 'number', message: 'è¾å¥åæ³çæ°å­'}
              ]">
                <el-input  maxlength="7"
                  v-model.number="updatePriceForm.items[index].discountAmount"
                  style="width:100px"
                ></el-input>
              </el-form-item>
              <span class="gray">ä¼æ å¡«å¥è´æ°ï¼å¢å å¡«å¥æ­£æ°</span>
            </td>
          </tr>
        </tbody>
      </table>
      <div class="clearfix" style="padding:6px 60px 9px 60px;">
        <el-form-item
          class="fr"
          label-width="80px"
          label="è¿è´¹"
          prop="orderFreight"
          :rules="[
            {required: true, message: 'æ­¤é¡¹å¿å¡«', trigger: 'blur' },
            {validator: (rule, value, callback)=>{validateNumber(rule, value, callback)},  trigger:'blur' },
            { type: 'number', message: 'è¾å¥åæ³çæ°å­'}
          ]">
          <el-input maxlength="7" v-model.number="updatePriceForm.orderFreight" style="width:100px;"></el-input>
        </el-form-item>
      </div>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="handleClose">å æ¶</el-button>
      <el-button type="primary" @click="updateOrderInfo">ç¡® å®</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { updateOrderAmount } from './../../../api/shop/order.js'
export default {
  data() {
    return {
      priceVisible: false,
      changeProductItems: [], // ä¿®æ¹ä»·æ ¼çéå
      updatePriceForm: {}
    }
  },
  methods: {
    setChangPrice(data) {
      this.changeProductItems = data.items
      const params = {
        orderId: data.id,
        orderFreight: data.orderFreight,
        items: []
      }
      data.items.forEach(v => {
        params.items.push({
          oderItemId: v.id,
          discountAmount: 0
        })
      })
      this.updatePriceForm = Object.assign({}, params)
      this.priceVisible = true
    },
    updateOrderInfo() {
      // æäº¤è®¢åä¿®æ¹ä¿¡æ¯
      this.$refs.form.validate(valid => {
        if (valid) {
          updateOrderAmount(this.updatePriceForm).then(res => {
            if (res.code === 200) {
              this.$successMessage('ä¿®æ¹æå')
              this.priceVisible = false
              this.$emit('change', true)
            } else {
              this.$errorMessage(res.message)
              this.$emit('change', false)
            }
          })
        }
      })
    },
    showAttr(data) {
      // è½¬æ¢jsonå­ç¬¦ä¸²å±æ§
      if (data === '') {
        return ''
      } else {
        const arr = JSON.parse(data)
        let str = ''
        arr.forEach(v => {
          const tmp = v.name + ':' + v.value + '  '
          str = str + tmp
        })
        return str
      }
    },
    validateNumber(rule, value, callback) {
      // éªè¯è¾å¥çå¼
      if (value >= 0) {
        callback()
      } else {
        callback(new Error('è¿è´¹ä¸è½ä¸ºè´æ°'))
      }
    },
    validatePrice(rule, value, callback, payAmount) {
      // éªè¯è¾å¥çå¼
      if (parseInt(value) + payAmount > 0) {
        callback()
      } else {
        callback(new Error('è®¢åæ»é¢ä¸è½å°äº0'))
      }
    },
    handleClose() {
      this.priceVisible = false
    }
  }
}
</script>

<style>
  .t-dialog .el-form-item__error{
    top: 90% !important;
    left: 0 !important;
  }
  .t-dialog .el-form-item__content{
    text-align: left;
  }
</style>



